:root {
  --primary-color: #2d79f3;
  --secondary-color: #151717;
  --background-color: #ffffff;
  --border-color: #ecedec;
  --font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  background-color: var(--background-color);
  padding: 30px;
  width: 450px;
  border-radius: 20px;
  font-family: var(--font-family);
}

::placeholder {
  font-family: var(--font-family);
}

.form button {
  align-self: flex-end;
}

.flex-column > label {
  color: var(--secondary-color);
  font-weight: 600;
}

.inputForm {
  border: 1.5px solid var(--border-color);
  border-radius: 10px;
  height: 50px;
  display: flex;
  align-items: center;
  padding-left: 10px;
  transition: 0.2s ease-in-out;
}

.input {
  margin-left: 10px;
  border-radius: 10px;
  border: none;
  width: 100%;
  height: 100%;
}

.input:focus {
  outline: none;
}

.inputForm:focus-within {
  border: 1.5px solid var(--primary-color);
}

.flex-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
}

.container {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 20px;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

.remember-me-text {
  margin-left: 8px;
  font-size: 14px;
  color: var(--secondary-color);
}

.span {
  font-size: 14px;
  margin-left: 5px;
  color: var(--primary-color);
  font-weight: 700;  /* Changed from 500 to 700 for bold text */
  cursor: pointer;
  text-decoration: none;
}

.span:hover {
  text-decoration: underline;
}

.button-submit {
  margin: 20px 0 10px 0;
  background-color: var(--secondary-color);
  border: none;
  color: white;
  font-size: 15px;
  font-weight: 500;
  border-radius: 10px;
  height: 50px;
  width: 100%;
  cursor: pointer;
}

.p {
  text-align: center;
  color: black;
  font-size: 14px;
  margin: 5px 0;
}

.container input {
  display: none;
}

.checkmark {
  position: relative;
  top: 0;
  left: 0;
  height: 1.05em;
  width: 1.0em;
  background-color: #2196F300;
  border-radius: 0.25em;
  transition: all 0.25s;
}

.container input:checked ~ .checkmark {
  background-color: #2196F3;
}

.checkmark:after {
  content: "";
  position: absolute;
  transform: rotate(0deg);
  border: 0.1em solid black;
  left: 0;
  top: 0;
  width: 1.05em;
  height: 1.05em;
  border-radius: 0.25em;
  transition: all 0.25s, border-width 0.1s;
}

.container input:checked ~ .checkmark:after {
  left: 0.40em;
  top: 0.25em;
  width: 0.25em;
  height: 0.5em;
  border-color: #fff0 white white #fff0;
  border-width: 0 0.15em 0.15em 0;
  border-radius: 0em;
  transform: rotate(45deg);
}

.toggle-password {
  background: none;
  border: none;
  cursor: pointer;
  margin-left: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  position: relative;
  top: -11px;
  right: 10px;
}